import React from 'react';
import { Accordion, AccordionItem } from 'react-light-accordion';
import 'react-light-accordion/demo/css/index.css';
import {NavLink} from 'react-router-dom';
import '../App.css';

const Acc = () => (
  <div>
    <Accordion atomic={true}>

    
      <AccordionItem type="button" title={<NavLink type="button" className="sidebaritem p-2 mb-3 fa fa-home "  to='/costumerdetails' style={{paddingTop:'20px'}}>Add Profile information</NavLink>} className=" p-2 mb-3 fa fa-home fa-lg  mx-sm-1"  style={{paddingTop:'20px'}}>                 
        <DummyContent1 />
      </AccordionItem>

      
      <AccordionItem type="button" title={<NavLink type="button" className="sidebaritem p-2 mb-3 fa fa-home "  to='/paymentdetails' style={{paddingTop:'20px'}}>Payment Transactions</NavLink>} className="sidebaritem p-2 mb-3 fa fa-home fa-lg  mx-sm-1"  style={{paddingTop:'20px'}}>                 
        <DummyContent2 />
      </AccordionItem>

    </Accordion>
  </div>
);

const DummyContent1 = () => (
  <p style={{ padding: '18px' }} className="dummy">
     &#62;&#62;&nbsp;&nbsp;Costumer Details
  </p>
);

const DummyContent2 = () => (
    <p style={{ padding: '18px' }} className="dummy">
       &#62;&#62;&nbsp;&nbsp;Payment Details
    </p>
  );

export default Acc;