import React,{Component} from 'react';
import '../App.css';

import {withRouter } from 'react-router-dom';
import { postImages,fetchImages } from '../redux/ActionCreaters';
import {connect} from 'react-redux';

const mapStateToProps = state=>
{
  return{
      images: state.Images
  }
}

const mapDispatchToProps = (dispatch)=>
{
  return (
    {
      postImages:(value)=>{dispatch(postImages(value))},
      fetchImages:()=>{dispatch(fetchImages())}
    }
  )
}



class Upload extends Component {
    constructor(props) {
      super(props);
      this.state = {
        file: '',
        imagePreviewUrl: ''
      };
      this._handleImageChange = this._handleImageChange.bind(this);
      this._handleSubmit = this._handleSubmit.bind(this);
      this.getimages = this.getimages.bind(this);
    }

    getimages(){
        this.props.fetchImages();
        //alert(JSON.stringify(this.props.images.images))
    }
  
    _handleSubmit(e) {
        this.props.postImages(this.state.imagePreviewUrl)
        document.getElementById("form-input").reset();
        this.setState({
          file: '',
          imagePreviewUrl: ''
        });
      e.preventDefault();
      
    }
  
    _handleImageChange(e) {
      e.preventDefault();
  
      let reader = new FileReader();
      let file = e.target.files[0];
  
      reader.onloadend = () => {
          this.setState({
              file: file,
              imagePreviewUrl: reader.result
            });
            //console.log('RESULT', reader.result)
      }
  
      reader.readAsDataURL(file)
    }
  
    render() {
      let {imagePreviewUrl} = this.state;
      let $imagePreview = null;
      if (imagePreviewUrl) {
        $imagePreview = (<img src={imagePreviewUrl} />);
      }

      const images = this.props.images.images.map((image) => {
          return(
              <div>
                  <img src={image.image} height='100' width='100' style={{paddingTop:'50px'}} alt="This is in .pdf format"></img>
              </div>
          )
      })
  
      return (
        <div style={{paddingLeft:'40px', paddingRight:'40px',paddingBottom:'50px', borderRadius:'12px'}}>
          <div className="incanvas">
                    <b>Images Upload</b>
          </div>

        <div className="component" style={{ fontSize:'20px', borderRadius:'12px',paddingLeft:'40px', paddingRight:'40px',paddingTop:'40px',borderRadius:'50px'}}>
          <form onSubmit={this._handleSubmit} id="form-input">
            <input className='buttonimg' type="file" onChange={this._handleImageChange} required/>
            <button className='buttonimg' type="submit" onClick={this._handleSubmit}>Upload File</button>
          </form>
          <img src={this.state.imagePreviewUrl} />

          <br/>
          <br/>
          <div>
              <button className='buttonimg' onClick={this.getimages}>See Uploaded Files</button>
              {images}
          </div>
          </div>
        </div>
      )
    }
  
  }


  export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Upload));