import * as ActionTypes from './ActionTypes';
import { costumers } from '../shared/costumers';
import { payments,total } from '../shared/payment';
import {baseUrl} from '../shared/baseUrl';


export const addCostumer = (costumer)=>
{
    return(
        {
            type:ActionTypes.ADD_COSTUMERS,
            payload:costumer
        }
    )
}

export const postCostumer = (values)=>(dispatch)=>{
const newCostumer ={
        id:values.sno,
        cat:values.cat,
        name:values.name,
        addr:values.addr,
        phno:values.phno,
        warehousesno:values.warehousesno,
        area:values.area,
        warehouses:values.warehouses,
        totalamt:0,
        notranx:0,
}

costumers.push(newCostumer)

return fetch(baseUrl + "costumers", {
        method: "POST",
        body: JSON.stringify(newCostumer),
        headers: {
          "Content-Type": "application/json"
        },
        credentials: "same-origin"
      })
        .then(
          response => {
            if (response.ok) {
              return response;
            } else {
              var error = new Error(
                "Error " + response.status + ": " + response.statusText
              );
              error.response = response;
              throw error;
            }
          },
          error => {
            throw error;
          }
        )
        .then(response => response.json())
        .then(response =>
          alert("Costumer Added!" + JSON.stringify(response))
        )
        .catch(error => {
          console.log("post Costumer", error.message);
          alert("Your Costumer could not be posted\nError: " + error.message);
        });
    };


export const costumerFailed = (errmess)=>({
        type:ActionTypes.COSTUMERS_FAILED,
        payload:errmess
})

export const costumersLoading=()=>({
        type:ActionTypes.COSTUMERS_LOADING
});



export const fetchCostumers = () => (dispatch) => {
        dispatch(costumersLoading(true));

    
        return fetch(baseUrl + 'costumers')
            .then(response => {
                if (response.ok) {
                    return response;
                } else {
                    var error = new Error('Error ' + response.status + ': ' + response.statusText);
                    error.response = response;
    
                    throw error;
                }
            },
                error => {
                    var errorMessage = new Error(error.errorMessage);
                    throw errorMessage;
                }
            )
            .then(response => response.json())
            .then(costumers=> dispatch(addCostumer(costumers)))
            .catch(error => dispatch(costumerFailed(error.message)))
    }



export const addPayment = (payment)=>
{
    return(
        {
            type:ActionTypes.ADD_PAYMENTS,
            payload:payment
        }
    )
}

export const updateCostumer = (value)=>
{
  return(
    {
      type : ActionTypes.UPDATE_COSTUMERS,
      payload:value
    }
  )
}

export const postPayment = (values)=>(dispatch)=>{
const name = values.name
const newPayment ={
        id:values.sno,
        cat:values.cat,
        name:values.name,
        tranxid:values.tranxid,
        amtsent:values.amt,
        type:values.type,
        phno:values.phno,
        otherinfo:values.other,
}
  
return (
  fetch(baseUrl + "costumers/")
    .then(response => response.json())
    .then(json => {
      try{
      let z = json.filter(a => a.name==values.name)[0]
    
      if(z)
      {
        fetch(baseUrl + "costumers/"+z.id, {
        method: "PATCH",
        body: JSON.stringify({notranx:z.notranx+1,totalamt:z.totalamt+parseInt(values.amt)}),
        headers: {
          "Content-Type": "application/json"
        },
        credentials: "same-origin"
        })
        .then(
          response => {
            if (response.ok) {
              return response;
            } else {
              var error = new Error(
                "Error " + response.status + ": " + response.statusText
              );
              error.response = response;
              throw error;
            }
          },
          error => {
            throw error;
          }
        )
        .then(response => response.json())
        .then(response =>
          alert("Costumer DaTa Updated!" + JSON.stringify(response))
        )
        .then( response =>
          {
            fetch(baseUrl + "payments", {
              method: "POST",
              body: JSON.stringify(newPayment),
              headers: {
                "Content-Type": "application/json"
              },
              credentials: "same-origin"
            })
              .then(
                response => {
                  if (response.ok) {
                    return response;
                  } else {
                    var error = new Error(
                      "Error " + response.status + ": " + response.statusText
                    );
                    error.response = response;
                    throw error;
                  }
                },
                error => {
                  throw error;
                }
              )
              .then(response => response.json())
              .then(response =>
                alert("Payment Added!" + JSON.stringify(response))
              )
              .catch(error => {
                console.log("post Payment", error.message);
                alert("Your Payment could not be posted\nError: " + error.message);
              })
          })
        .catch(error => {
          console.log("Update Costumer", error.message);
          alert("Your Costumer Data could not be updated\nError: " + error.message);
        })

        
      
      }
        else{
          throw 1
        }
      }
      catch(err){
        alert("No such Costumer exists in the database")
      }
    
    })  

  
  )
};


export const paymentFailed = (errmess)=>({
        type:ActionTypes.PAYMENTS_FAILED,
        payload:errmess
})

export const paymentsLoading=()=>({
        type:ActionTypes.PAYMENTS_LOADING
});

export const fetchPayments = () => (dispatch) => {
  dispatch(paymentsLoading(true));

  return fetch(baseUrl + 'payments')
      .then(response => {
          if (response.ok) {
              return response;
          } else {
              var error = new Error('Error ' + response.status + ': ' + response.statusText);
              error.response = response;

              throw error;
          }
      },
          error => {
              var errorMessage = new Error(error.errorMessage);
              throw errorMessage;
          }
      )
      .then(response => response.json())
      .then(payments=> dispatch(addPayment(payments)))
      .catch(error => dispatch(paymentFailed(error.message)))
}


export const addImages = (image)=>
{
    return(
        {
            type:ActionTypes.ADD_IMAGES,
            payload:image
        }
    )
}


export const postImages = (value)=>(dispatch)=>{
  const newImage ={
    image : value
  }
  
  return fetch(baseUrl + "images", {
          method: "POST",
          body: JSON.stringify(newImage),
          headers: {
            "Content-Type": "application/json"
          },
          credentials: "same-origin"
        })
          .then(
            response => {
              //alert(JSON.stringify(newImage))
              if (response.ok) {
                return response;
              } else {
                var error = new Error(
                  "Error " + response.status + ": " + response.statusText
                );
                error.response = response;
                throw error;
              }
            },
            error => {
              throw error;
            }
          )
          .then(response => response.json())
          .then(response =>
            alert("Image Added!" )
          )
          .catch(error => {
            console.log("post Image", error.message);
            alert("Your Image could not be posted\nError: " + error.message);
          });
};

export const imagesFailed = (errmess)=>({
  type:ActionTypes.IMAGES_FAILED,
  payload:errmess
})

export const imagesLoading=()=>({
  type:ActionTypes.IMAGES_LOADING
});

  

export const fetchImages = () => (dispatch) => {
  dispatch(imagesLoading(true));
  
  return fetch(baseUrl + 'images')
    .then(response => {
      if (response.ok) {
        return response;
      } else {
        var error = new Error('Error ' + response.status + ': ' + response.statusText);
        error.response = response;
      
        throw error;
      }
    },
    error => {
      var errorMessage = new Error(error.errorMessage);
      throw errorMessage;
    }
    )
    .then(response => response.json())
    .then(images=> dispatch(addImages(images)))
    .catch(error => dispatch(imagesFailed(error.message)))
}
